* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
}

.App {
  background-color: rgb(194, 194, 194);
  padding: 2rem 1.5rem;
  border-radius: .5rem;
  box-shadow: 5px 5px 25px rgba(0,0,0,0.3);
}

.App form input {
  height: 2rem;
  width: 15rem;
  border-radius: 5px;
  border: none;
  padding: 0 10px;
  margin-right: 10px;
  background-color: white;
}

.App form button {
  height: 2rem;
  padding: 0 10px;
  border-radius: 5px;
  border: none;
  color: white;
  background-color: rgb(107, 106, 106);
  margin-bottom: 1.2rem;
}

.App .list {
  margin-top: .8rem;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  color: white;
  border-radius: 5px;
  background-color: rgb(161, 160, 160);
} 

.App .list span {
  cursor: pointer;
}

.App .list span:hover .biicon {
  color: red;
}

.App p {
  margin-top: 2rem;
  color: rgb(107, 106, 106);
}